import Vue from 'vue';
import clogSysbols from 'log-symbols';

const isProduction = process.env.NODE_ENV === 'production';

Vue.config.silent = isProduction; // vue    
Vue.config.devtools = !isProduction; //   
Vue.config.errorHandler = (err, vm, info) => {
	/** In case of production replace the functions definition */
	console.error(err);
	console.error(vm);
	console.error(info);
	//  
	// `type` Vue  . :  
	// 2.2.0+    
};

Vue.config.warnHandler = (msg, vm, trace) => {
	console.error(msg);
	console.error(vm);
	console.error(trace);
	// trace    .
	//  Vue      .
	//      .
};

Vue.config.performance = !isProduction;
// const version = Number(Vue.version.split(".")[0]);

console.log(
	'%c XAIOps',
	'font-weight: bold; font-size: 50px;color: red; text-shadow: 3px 3px 0 rgb(217,31,38) , 6px 6px 0 rgb(226,91,14) , 9px 9px 0 rgb(245,221,8) , 12px 12px 0 rgb(5,148,68) , 15px 15px 0 rgb(2,135,206) , 18px 18px 0 rgb(4,77,145) , 21px 21px 0 rgb(42,21,113)',
);
console.log(`%c ${clogSysbols.info}#### vue version: ${Vue.version} `, 'color: #4a9663');
